import React, { Component } from "react"
import Navbar from "../component/Navbar"
import axios from "axios"
import Details from "../component/Details";

class index extends Component {
  state = {
    selectedCountry: null
  }
  clearSelection = () => {
    this.setState({
      selectedCountry: null
    })
  }
  handleClick = ()=>{

  }
  renderCountry = each => {
    return <li key={each.currency} onClick="this.handleClick(this,{each.currency})">{each.country}</li>
  }
  render() {
    return (
      <>
        <Navbar />
        <div className="container">
          {
            this.selectedCountry ? <Details />: <ul>{this.props.counties.map(each => this.renderCountry(each))}</ul>
          }
        </div>
      </>
    )
  }
}
index.getInitialProps = async () => {
  const res = await axios.get(
    "https://api.coindesk.com/v1/bpi/supported-currencies.json"
  )
  return {
    counties: res.data
  }
}
export default index
